
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    color: {
      required: false,
      type: String,
      default: "#233E86",
    },
    rotation: {
      required: false,
      type: Number,
      default: 40,
    },
  },
  computed: {
    style() {
      return {
        "--start-rotation": `${this.rotation}deg`,
        "--end-rotation": `${this.rotation + 360}deg`,
      };
    },
  },
});
